/* eslint-disable no-continue */
/* eslint-disable guard-for-in */
/* eslint-disable no-restricted-syntax */
import { createAsyncThunk } from '@reduxjs/toolkit'
import qs from 'qs'
import { AxiosError } from 'axios'
import {
  API_HR_PART_ABOUT_VACANCY_STUDENT_ENDPOINT,
  API_HR_PART_VACANCY_STUDENT_ENDPOINT,
  API_PARTNER_DIRECTORY_ENDPOINT,
  API_PARTNER_VACANCY_RESPONSE_ENDPOINT,
  API_STUDENT_EXTERNAL_VACANCY,
  API_VACANCY_RESPONSE_ENDPOINT,
} from '../../utils/constants'
import { TStudentVacancyRequestParams } from '../types/student-vacancies/student-vacancies-types'
import api from './settings'

export const getPartnersDirectories = createAsyncThunk(
  'partners/getDirectories',
  async (_: void, { rejectWithValue }) => {
    try {
      const response = await api.get(API_PARTNER_DIRECTORY_ENDPOINT)
      return response.data
    } catch (error) {
      const typedError = error as AxiosError
      return rejectWithValue(typedError?.response?.data)
    }
  },
)

const paramsSerializer = (params: TStudentVacancyRequestParams) => qs.stringify(params, { arrayFormat: 'repeat' })

const getParams = (params: TStudentVacancyRequestParams) => {
  // eslint-disable-next-line no-var
  var modifiedParams = new URLSearchParams('')
  for (const key in params) {
    const value = params[key as keyof TStudentVacancyRequestParams] as any
    if (key === 'cities' && value.length > 0) {
      value.forEach((city: string) => {
        modifiedParams.append('cities', city)
      })
      // eslint-disable-next-line no-continue
      continue
    }
    if (key === 'currency') {
      if (value.dollar) {
        modifiedParams.append('currency', '1')
      }
      if (value.euro) {
        modifiedParams.append('currency', '2')
      }
      if (value.pound) {
        modifiedParams.append('currency', '3')
      }
      if (value.ruble) {
        modifiedParams.append('currency', '4')
      }
      if (value.shekel) {
        modifiedParams.append('currency', '5')
      }
      continue
    }
    if (key === 'employment') {
      if (value.office) {
        modifiedParams.append('employment', '1')
      }
      if (value.remote) {
        modifiedParams.append('employment', '3')
      }
      if (value.hybrid) {
        modifiedParams.append('employment', '2')
      }
      continue
    }
    if (key === 'designFormats') {
      if (value.dogovor) {
        modifiedParams.append('design_formats', '1')
      }
      if (value.samo) {
        modifiedParams.append('design_formats', '2')
      }
      if (value.ip) {
        modifiedParams.append('design_formats', '3')
      }
      if (value.contract) {
        modifiedParams.append('design_formats', '4')
      }
      if (value.gph) {
        modifiedParams.append('design_formats', '5')
      }
      continue
    }
    //    
    if (key === 'status') continue
    if (value !== null && value !== undefined) {
      modifiedParams.append(key, value)
    }
  }
  //     
  if (params?.status) {
    //   
    if (params.status?.new && !params.status?.is_accepted && !params.status?.is_showed) {
      modifiedParams.append('is_showed', 'false')
      modifiedParams.append('is_accepted', 'false')
      return modifiedParams.toString()
    }
    //   
    if (!params.status?.new && !params.status?.is_accepted && params.status?.is_showed) {
      modifiedParams.append('is_showed', 'true')
      modifiedParams.append('is_accepted', 'false')
      return modifiedParams.toString()
    }
    //   
    if (!params.status?.new && params.status?.is_accepted && !params.status?.is_showed) {
      modifiedParams.append('is_showed', 'true')
      modifiedParams.append('is_accepted', 'true')
      return modifiedParams.toString()
    }
    //  ,    
    if (params.status?.new && params.status?.is_accepted && params.status?.is_showed) {
      return modifiedParams.toString()
    }

    //    
    if (params.status?.new && !params.status?.is_accepted && params.status?.is_showed) {
      modifiedParams.append('is_accepted', 'false')
      return modifiedParams.toString()
    }

    //       
    if (params.status?.new && params.status?.is_accepted && !params.status?.is_showed) {
      return modifiedParams.toString()
    }

    //       ,  
    if (params.status?.is_showed) modifiedParams.append('is_showed', params.status?.is_showed.toString())
    if (params.status?.is_accepted) modifiedParams.append('is_accepted', params.status?.is_accepted.toString())
  }
  return modifiedParams.toString()
}

export const getPartnersVacancies = createAsyncThunk(
  'partners/getPartnersVacancies',
  async (requestParams: TStudentVacancyRequestParams, { rejectWithValue }) => {
    const params = getParams(requestParams)
    try {
      const response = await api.get(`${API_HR_PART_VACANCY_STUDENT_ENDPOINT}?${params}`, {
        params,
        paramsSerializer,
      })
      return response.data
    } catch (error) {
      const typedError = error as AxiosError
      return rejectWithValue(typedError?.response?.data)
    }
  },
)

