import { createSlice } from '@reduxjs/toolkit'
import { IAPIError, IRequestInfo, TApiReference } from '../types/global-types'
import {
  createVacancyResponse,
  getExternalVacancies,
  getExternalVacancy,
  getPartnersDirectories,
  getPartnersVacancies,
  getPartnersVacancy,
  postPartnersVacancyResponse,
} from '../axios/student-vacancies'
import { IStudentVacanciesDirectory, IStudentVacancy } from '../types/student-vacancies/student-vacancies-types'

interface IPartnersState extends IRequestInfo {
  directories: IStudentVacanciesDirectory
  vacancies: IStudentVacancy[]
  vacancyPage: string | null | undefined
  externalVacancies: IStudentVacancy[]
  externalPage: string | null | undefined
  selectedVacancy: IStudentVacancy | null
  isJobApplicationModalOpen: boolean
  achievements: TApiReference[]
  isOpenVacancyPopup: boolean
  isOpenPartnersVacancyPopup: boolean
  selectedFullVacancy: IStudentVacancy | null
  isNoDetails: boolean
  filteredVacancies: IStudentVacancy[]
  isExternalLoading: boolean
  isVacancyLoading: boolean
}

export const initialState: IPartnersState = {
  status: 'idle',
  isError: false,
  errorInfo: null,
  directories: {
    courses: [],
    currencies: [],
    jobApplicationItems: [],
    employmentItems: [],
    workFormatItems: [],
  },
  vacancies: [],
  vacancyPage: undefined,
  externalVacancies: [],
  externalPage: undefined,
  selectedVacancy: null,
  isJobApplicationModalOpen: false,
  achievements: [],
  isOpenVacancyPopup: false,
  isOpenPartnersVacancyPopup: false,
  selectedFullVacancy: null,
  //       
  isNoDetails: false,
  filteredVacancies: [],
  isExternalLoading: false,
  isVacancyLoading: false,
}

const studentVacanciesSlice = createSlice({
  name: 'partners',
  initialState,
  reducers: {
    openJobApplicationModal(state) {
      state.isJobApplicationModalOpen = true
    },
    closeJobApplicationModal(state) {
      state.isJobApplicationModalOpen = false
    },
    openVacancyPopup(state) {
      state.isOpenVacancyPopup = true
    },
    closeVacancyPopup(state) {
      state.isOpenVacancyPopup = false
      //    is_viewed   
      const index = state.externalVacancies.findIndex((vacancy) => vacancy?.id === state.selectedFullVacancy?.id)
      if (!state.externalVacancies[index].is_viewed) state.externalVacancies[index].is_viewed = true
      state.selectedFullVacancy = null
      state.selectedVacancy = null
    },
    openPartnersVacancyPopup(state) {
      state.isOpenPartnersVacancyPopup = true
    },
    closePartnersVacancyPopup(state) {
      state.isOpenPartnersVacancyPopup = false
      //    is_viewed   
      const index = state.vacancies.findIndex((vacancy) => vacancy?.id === state.selectedFullVacancy?.id)
      if (!state.vacancies[index].is_viewed) state.vacancies[index].is_viewed = true
      // ,           
      // state.selectedFullVacancy = null
      // state.selectedVacancy = null
    },
    selectVacancy(state, action) {
      state.selectedVacancy = action.payload
    },
    resetPartnersAchievements(state) {
      state.achievements = []
    },
    resetPage(state) {
      state.externalPage = undefined
      state.vacancyPage = undefined
    },
    resetPartnersPage(state) {
      state.vacancyPage = undefined
    },
  },
  extraReducers: (builder) => {
    builder
      // Get partners directories
      .addCase(getPartnersDirectories.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
      })
      .addCase(getPartnersDirectories.fulfilled, (state, action) => {
        const rawData = action.payload
        state.status = 'succeeded'
        state.directories.courses = rawData.courses
        state.directories.currencies = rawData?.currencies?.map((currency: TApiReference) => ({
          id: currency.id,
          name: currency.name,
          icon: currency.label,
        }))
        state.directories.employmentItems = rawData?.employment?.map((e: TApiReference) => ({ ...e, name: e?.value }))
        state.directories.workFormatItems = rawData?.work_format?.map((w: TApiReference) => ({ ...w, name: w?.value }))
        state.directories.jobApplicationItems = rawData?.design_formats?.map((d: TApiReference) => ({
          ...d,
          name: d?.value,
        }))
      })
      .addCase(getPartnersDirectories.rejected, (state, action) => {
        state.isError = true
        state.errorInfo = action.payload as IAPIError
        state.status = 'failed'
      })

      // Get partners vacancies
      .addCase(getPartnersVacancies.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
        state.isVacancyLoading = true
      })
      .addCase(getPartnersVacancies.fulfilled, (state, action) => {
        state.status = 'succeeded'
        const searchParams = new URLSearchParams(action.payload?.next)
        const page = searchParams.get('page')
        if (state.vacancyPage) {
          state.vacancies.push(...action.payload?.results)
        } else {
          state.vacancies = action.payload?.results
        }
        state.vacancyPage = page
        if (action.payload?.results?.length > 0 && !page) {
          const [firstVacancy] = action.payload?.results
          state.selectedVacancy = firstVacancy
        }
        state.isVacancyLoading = false
      })
      .addCase(getPartnersVacancies.rejected, (state, action) => {
        state.isError = true
        state.errorInfo = action.payload as IAPIError
        state.status = 'failed'
      })

      // Get external vacancies
      .addCase(getExternalVacancies.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
        state.isExternalLoading = true
      })
      .addCase(getExternalVacancies.fulfilled, (state, action) => {
        state.status = 'succeeded'
        const searchParams = new URLSearchParams(action.payload?.next)
        const page = searchParams.get('page')
        if (state.externalPage) {
          state.externalVacancies.push(...action.payload?.results)
        } else {
          state.externalVacancies = action.payload?.results
        }
        state.externalPage = page
        state.isExternalLoading = false
      })
      .addCase(getExternalVacancies.rejected, (state, action) => {
        state.isError = true
        state.errorInfo = action.payload as IAPIError
        state.status = 'failed'
        state.isExternalLoading = false
      })

      // Get partners vacancy
      .addCase(getPartnersVacancy.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
      })
      .addCase(getPartnersVacancy.fulfilled, (state, action) => {
        state.status = 'succeeded'
        state.selectedFullVacancy = action.payload
        // if (action.payload.length > 0) {
        //   const [firstVacancy] = action.payload
        //   state.selectedVacancy = firstVacancy
        // }
      })
      .addCase(getPartnersVacancy.rejected, (state, action) => {
        state.isError = true
        state.errorInfo = action.payload as IAPIError
        state.status = 'failed'
      })

      // Get partners vacancy
      .addCase(getExternalVacancy.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
      })
      .addCase(getExternalVacancy.fulfilled, (state, action) => {
        state.status = 'succeeded'
        state.selectedFullVacancy = action.payload
        // if (action.payload.length > 0) {
        //   const [firstVacancy] = action.payload
        //   state.selectedVacancy = firstVacancy
        // }
      })
      .addCase(getExternalVacancy.rejected, (state, action) => {
        state.isError = true
        state.errorInfo = action.payload as IAPIError
        state.status = 'failed'
      })

      // Post partners vacancy response
      .addCase(postPartnersVacancyResponse.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
      })
      .addCase(postPartnersVacancyResponse.fulfilled, (state, action) => {
        state.status = 'succeeded'
        const newVacancies = state.vacancies.map((v) => {
          if (v.id === state.selectedVacancy?.id) return { ...v, is_accepted: true }
          return v
        })
        state.achievements = action.payload.achievements
        state.vacancies = newVacancies
      })
      .addCase(postPartnersVacancyResponse.rejected, (state, action) => {
        state.isError = true
        state.errorInfo = action.payload as IAPIError
        state.status = 'failed'
      })

      // Post external vacancy response
      .addCase(createVacancyResponse.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
      })
      .addCase(createVacancyResponse.fulfilled, (state, action) => {
        state.status = 'succeeded'
        // state.achievements = action.payload.achievements
        state.externalVacancies = state.externalVacancies?.map((v) => {
          if (v.id === state.selectedFullVacancy?.id) {
            return { ...v, is_accepted: true }
          }
          return v
        })
      })
      .addCase(createVacancyResponse.rejected, (state, action) => {
        state.isError = true
        state.errorInfo = action.payload as IAPIError
        state.status = 'failed'
      })
  },
})

export const {
  openJobApplicationModal,
  closeJobApplicationModal,
  selectVacancy,
  resetPartnersAchievements,
  openVacancyPopup,
  closeVacancyPopup,
  openPartnersVacancyPopup,
  closePartnersVacancyPopup,
  resetPage,
  resetPartnersPage,
} = studentVacanciesSlice.actions

export default studentVacanciesSlice
