import { createAsyncThunk } from '@reduxjs/toolkit'
import { AxiosError } from 'axios'
import { API_ONBOARDING_DIRECTORY_ENDPOINT } from '../../utils/constants'
import api from './settings'

export const getOnboardingBaseData = createAsyncThunk(
  'onboarding/getOnboardingBaseData',
  async (_: void, { rejectWithValue }) => {
    try {
      const response = await api.get(API_ONBOARDING_DIRECTORY_ENDPOINT)
      return response.data
    } catch (error) {
      const typedError = error as AxiosError
      return rejectWithValue(typedError?.response?.data)
    }
  },
)
