import React from 'react'
import './index.css'
import * as Sentry from '@sentry/react'
import { BrowserTracing } from '@sentry/tracing'
// Import Swiper styles
import 'swiper/swiper-bundle.min.css'
import 'swiper/swiper.min.css'
import ReactDOM from 'react-dom'
import { BrowserRouter } from 'react-router-dom'
import { Provider } from 'react-redux'
import { setupStore } from './services/store/store'
import App from './components/App/App'
import reportWebVitals from './reportWebVitals'

const store = setupStore()

const { REACT_APP_SENTRY_PROJECT, REACT_APP_SENTRY_RELEASE } = process.env

if (REACT_APP_SENTRY_PROJECT) {
  // eslint-disable-next-line no-nested-ternary
  const dsnUrl = process.env.REACT_APP_SENTRY_DSN
  Sentry.init({
    dsn: dsnUrl,
    integrations: [new BrowserTracing()],
    release: REACT_APP_SENTRY_RELEASE,
    // eslint-disable-next-line no-nested-ternary
    environment: REACT_APP_SENTRY_PROJECT !== 'students-diary-front-prod' ? 'dev' : 'prod',
    //   debug ,    
    debug: REACT_APP_SENTRY_PROJECT !== 'students-diary-front-prod',
    //    "  "
    // eslint-disable-next-line no-unused-vars
    beforeSend(event, hint) {
      // Check if it is an exception, and if so, show the report dialog
      if (event.exception) {
        Sentry.showReportDialog({ eventId: event.event_id })
      }
      return event
    },
    // We recommend adjusting this value in production, or using tracesSampler
    // for finer control
    //  0.7,    429 
    tracesSampleRate: 0.7,
    // ,    -  ResizeObserver
    //  -     
    ignoreErrors: ['ResizeObserver loop limit exceeded'],
  })
}

ReactDOM.render(
  <React.StrictMode>
    <Sentry.ErrorBoundary fallback={<p>-   .  </p>}>
      <BrowserRouter>
        <Provider store={store}>
          <App />
        </Provider>
      </BrowserRouter>
    </Sentry.ErrorBoundary>
  </React.StrictMode>,
  document.getElementById('root'),
)

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals()
