import React from 'react'
import { AnyAction, ThunkDispatch } from '@reduxjs/toolkit'
import { enqueueSnackbar } from '../services/slices/app-slice'
import { ApiStage } from '../services/types/achievements/achievements-types'

const showAchievementsNotifications = (
  achievements: Array<ApiStage>,
  dispatch: ThunkDispatch<unknown, unknown, AnyAction>,
) => {
  if (achievements) {
    achievements.forEach((achievement, index) => {
      setTimeout(() => {
        dispatch(
          enqueueSnackbar({
            info: {
              type: 'achievement',
              title: achievement.name,
              message: achievement.condition_text,
              image: achievement.success_image,
            },
            options: {
              key: new Date().getTime() + Math.random(),
              autoHideDuration: 6000,
            },
          }),
        )
      }, 500 * (index + 1))
    })
  }
}

export default showAchievementsNotifications
