import axios from 'axios'
import {
  API_TOKEN_REFRESH_ENDPOINT,
  API_SIGN_IN_ENDPOINT,
  API_SIGN_UP_ENDPOINT,
  API_PASSWORD_RECOVERY_ENDPOINT,
} from '../../utils/constants'

const instance = axios.create({
  baseURL: process.env.REACT_APP_API,
  headers: {
    'Content-Type': 'application/json',
    Accept: 'application/json',
  },
  withCredentials: true,
})

instance.interceptors.response.use(
  (res) => res,
  async (err) => {
    const originalConfig = err.config
    if (originalConfig.url === API_TOKEN_REFRESH_ENDPOINT && err.response.status === 401) return Promise.reject(err)

    if (
      originalConfig.url !== API_SIGN_IN_ENDPOINT &&
      originalConfig.url !== API_SIGN_UP_ENDPOINT &&
      originalConfig.url !== API_PASSWORD_RECOVERY_ENDPOINT &&
      err.response
    ) {
      // eslint-disable-next-line no-underscore-dangle
      if (err.response.status === 401 && !originalConfig.retry) {
        originalConfig.retry = true

        try {
          await instance.post(API_TOKEN_REFRESH_ENDPOINT)
          if (originalConfig.data) {
            const config = { ...originalConfig, data: originalConfig.data }
            return instance(config)
          }
          return instance(originalConfig)
        } catch (error) {
          return Promise.reject(error)
        }
      }
    }
    //  - 404    (     )
    // if (err?.response?.status === 404) {
    //   window.location.href = '/404'
    // }

    if (err?.response?.status === 500) {
      window.location.href = '/500'
    }

    return Promise.reject(err)
  },
)

