import React from 'react'
import { getStatus } from '../services/axios/auth'
import { useAppDispatch } from '../services/store/hooks'
import { STATUS_CHECK_PERIOD } from './constants'
import { enqueueSnackbar } from '../services/slices/app-slice'
import { setTimeoutId } from '../services/slices/auth-slice'

type TUseUpdateStudentStatus = () => { updateStudentStatus: () => void }

const useUpdateStudentStatus: TUseUpdateStudentStatus = () => {
  const dispatch = useAppDispatch()

  const request = () => {
    try {
      dispatch(getStatus())
    } catch {
      dispatch(
        enqueueSnackbar({
          info: {
            type: 'error',
            title: '  ',
            message: '  ',
          },
          options: {
            key: new Date().getTime() + Math.random(),
            autoHideDuration: 6000,
          },
        }),
      )
    }
    dispatch(setTimeoutId(setTimeout(request, STATUS_CHECK_PERIOD)))
  }
  return {
    updateStudentStatus: () => {
      dispatch(setTimeoutId(setTimeout(request, STATUS_CHECK_PERIOD)))
    },
  }
}

export default useUpdateStudentStatus
