import { createAsyncThunk } from '@reduxjs/toolkit'
import { AxiosError } from 'axios'
import {
  API_SIGN_IN_ENDPOINT,
  API_SIGN_OUT_ENDPOINT,
  API_SIGN_UP_ENDPOINT,
  API_PROFILE_ENDPOINT,
  API_USER_PROFILE_ENDPOINT,
  API_PASSWORD_RECOVERY_ENDPOINT,
  API_CREATE_NEW_PASSWORD_ENDPOINT,
  API_USER_STATUS_ENDPOINT,
} from '../../utils/constants'
import { TSignInData, TSignUpData, TResetPasswordData, TCreatePasswordData } from '../types/auth/auth-types'
import api from './settings'

export const signIn = createAsyncThunk('auth/signIn', async (data: TSignInData, { rejectWithValue }) => {
  try {
    const response = await api.post(API_SIGN_IN_ENDPOINT, data)
    return response.data
  } catch (error) {
    const typedError = error as AxiosError
    return rejectWithValue(typedError?.response?.data)
  }
})

