export const getProfile = createAsyncThunk('auth/getProfile', async (_: void, { rejectWithValue }) => {
  try {
    const response = await api.get(API_USER_PROFILE_ENDPOINT)
    return response.data
  } catch (error) {
    const typedError = error as AxiosError
    return rejectWithValue(typedError?.response?.data)
  }
})

export const postOnboarding = createAsyncThunk('auth/postOnboarding', async (data: FormData, { rejectWithValue }) => {
  try {
    const response = await api.post(API_PROFILE_ENDPOINT, data, {
      headers: {
        'Content-Type': 'multipart/form-data',
        Accept: 'application/json',
      },
    })
    return response.data
  } catch (error) {
    const typedError = error as AxiosError
    return rejectWithValue(typedError?.response?.data)
  }
})

