import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { ExperienceInfo, OnboardingBaseData, PersonalInfo, ResumeInfo } from '../types/onboarding/onboarding-types'
import { getOnboardingBaseData } from '../axios/onboarding'
import { IAPIError, IRequestInfo } from '../types/global-types'

type TInitialState = IRequestInfo & {
  baseData: OnboardingBaseData | null
  personalInfo: PersonalInfo | null
  experienceInfo: ExperienceInfo | null
  resumeInfo: ResumeInfo | null
}

const initialState: TInitialState = {
  status: 'idle',
  isError: false,
  errorInfo: null,
  baseData: null,
  personalInfo: null,
  experienceInfo: null,
  resumeInfo: null,
}

export const onboardingSlice = createSlice({
  name: 'onboarding',
  initialState,
  reducers: {
    setPersonalInfo: (state, action: PayloadAction<PersonalInfo>) => {
      state.personalInfo = action.payload
    },
    setExperienceInfo: (state, action: PayloadAction<ExperienceInfo>) => {
      state.experienceInfo = action.payload
    },
    setResumeInfo: (state, action: PayloadAction<ResumeInfo>) => {
      state.resumeInfo = action.payload
    },
    clearOnboardingInfo: (state) => {
      state.resumeInfo = null
      state.personalInfo = null
      state.experienceInfo = null
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(getOnboardingBaseData.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
      })
      .addCase(getOnboardingBaseData.fulfilled, (state, action) => {
        state.status = 'succeeded'
        state.baseData = action.payload
      })
      .addCase(getOnboardingBaseData.rejected, (state, action) => {
        state.status = 'failed'
        state.isError = true
        state.errorInfo = action.payload as IAPIError
      })
  },
})

export const { setPersonalInfo, setExperienceInfo, setResumeInfo, clearOnboardingInfo } = onboardingSlice.actions
export default onboardingSlice
