import { createSlice, PayloadAction } from '@reduxjs/toolkit'

type TInitialState = {
  isHidden: boolean
}

const initialState: TInitialState = {
  isHidden: false,
}

export const statisticsMetricsSlice = createSlice({
  name: 'statistics-metrics-slice',
  initialState,
  reducers: {
    changeStatisticsMetricsState: (state, action: PayloadAction<boolean>) => {
      state.isHidden = action.payload
    },
  },
})

export const { changeStatisticsMetricsState } = statisticsMetricsSlice.actions
export default statisticsMetricsSlice
