import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { useAppDispatch } from '../store/hooks'

interface IWebsocketState {
  wsConnected: boolean
  wsError: boolean
}

const initialState = {
  wsConnected: false,
  wsError: false,
} as IWebsocketState

const websocketSlice = createSlice({
  name: 'ws',
  initialState,
  reducers: {
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    wsConnectionStart(state, action: PayloadAction<{ url: string; token?: string }>) {},

    wsConnectionStop(state) {
      state.wsConnected = false
      state.wsError = false
    },

    wsConnectionSuccess(state) {
      state.wsConnected = true
      state.wsError = false
    },

    wsConnectionError(state) {
      state.wsConnected = false
      state.wsError = true
    },

    wsConnectionClosed(state) {
      state.wsConnected = false
      state.wsError = false
    },
  },
})

export default websocketSlice

export const openSocket =
  () =>
  (dispatch = useAppDispatch()) =>
    dispatch(websocketSlice.actions.wsConnectionStart({ url: `${process.env.REACT_APP_WS}/ws/achievements` }))

export const closeSocket =
  () =>
  (dispatch = useAppDispatch()) => {
    dispatch(websocketSlice.actions.wsConnectionStop())
  }
