import { createAsyncThunk } from '@reduxjs/toolkit'
import { AxiosError } from 'axios'
import {
  API_HR_PART_VACANCY_ENDPOINT,
  API_HR_PART_DIRECTORY_ENDPOINT,
  API_HR_PART_VACANCY_BY_ID_ENDPOINT,
  API_STUDENT_BY_ID_ENDPOINT,
  API_HR_PART_VACANCY_INFO_ENDPOINT,
} from '../../utils/constants'
import api from './settings'
import {
  IDirectoriesResponse,
  IParams,
  TGetVacanciesData,
  TGetVacancyInfo,
  TPatchData,
  TPatchStudentData,
} from '../types/vacacies-page-types/vacancies-page-types'

export const getVacanciesList = createAsyncThunk(
  'vacancies-page/getVacanciesList',
  async (data: TGetVacanciesData, { rejectWithValue }) => {
    try {
      const { paramsSerializer } = data
      const response = await api.get(API_HR_PART_VACANCY_ENDPOINT, {
        params: { ...data.hook.filters },
        paramsSerializer,
      })
      // return Promise.resolve(response.data)
      return response?.data
    } catch (e) {
      const err = e as AxiosError
      return rejectWithValue(err?.response)
    }
  },
)

export const getDirectories = createAsyncThunk(
  'vacancies-page/getDirectories',
  async (_: void, { rejectWithValue }) => {
    try {
      const response = await api.get(API_HR_PART_DIRECTORY_ENDPOINT)
      const rawData: IDirectoriesResponse = response?.data
      const courses = rawData?.courses
      const statuses = rawData?.vacancies_status?.map((s) => ({ ...s, label: s?.value }))
      const studentStatuses = rawData?.student_status?.map((s) => ({ ...s, label: s?.value }))
      const currencies = rawData?.currencies?.map((currency) => ({
        id: currency.id,
        name: currency.name,
        icon: currency.label,
      }))
      const employmentItems = rawData?.employment?.map((e) => ({ ...e, name: e?.value }))
      const workFormatItems = rawData?.work_format?.map((w) => ({ ...w, name: w?.value }))
      const jobApplicationItems = rawData?.design_formats?.map((d) => ({ ...d, name: d?.value }))
      const experienceItems = rawData?.experience_types
      const educationItems = rawData?.education_types
      // return Promise.resolve(response.data)
      return {
        filters: {
          courses,
          statuses,
        },
        directoriesData: {
          courses,
          statuses,
          studentStatuses,
          currencies,
          employmentItems,
          workFormatItems,
          jobApplicationItems,
          experienceItems,
          educationItems,
        },
      }
    } catch (e) {
      const typedError = e as AxiosError
      return rejectWithValue(typedError?.response)
    }
  },
)

export const patchVacancyStatus = createAsyncThunk(
  'vacancies-page/patchVacancyStatus',
  async (data: TPatchData, { rejectWithValue }) => {
    try {
      const response = await api.patch(API_HR_PART_VACANCY_BY_ID_ENDPOINT(data.vacancyId), {
        vacancy_status: data.status.status,
      })
      return {
        updatedRow: response.data,
      }
    } catch (e) {
      const typedError = e as AxiosError
      return rejectWithValue(typedError?.response)
    }
  },
)

export const patchStudentStatus = createAsyncThunk(
  'vacancies-page/patchStudentStatus',
  async (data: TPatchStudentData, { rejectWithValue }) => {
    try {
      const response = await api.patch(API_STUDENT_BY_ID_ENDPOINT(data.studentId), data.status)
      return {
        updatedRow: response.data,
      }
    } catch (e) {
      const typedError = e as AxiosError
      return rejectWithValue(typedError?.response)
    }
  },
)

export const getInfo = createAsyncThunk(
  'vacancies-page/getInfo',
  async (data: TGetVacancyInfo, { rejectWithValue }) => {
    const params: IParams = {}
    if (data.searchQuery) params.search = data.searchQuery
    try {
      const response = await api.get(API_HR_PART_VACANCY_INFO_ENDPOINT(data.id), {
        params,
      })
      return response.data
    } catch (e) {
      const typedError = e as AxiosError
      return rejectWithValue(typedError?.response)
    }
  },
)
