import { createSlice } from '@reduxjs/toolkit'
import {
  createPassword,
  getProfile,
  getStatus,
  postOnboarding,
  resetPassword,
  signIn,
  signOut,
  signUp,
} from '../axios/auth'
import { IAuthState, IAPIError, IRequestInfo, TProfile } from '../types/auth/auth-types'

const initialRequestInfoState: IRequestInfo = {
  status: 'idle',
  isError: false,
  errorInfo: null,
}

export const initialState: IAuthState = {
  isLoggedIn: false,
  profile: undefined,
  role: null,
  timeoutId: undefined,
  signInRequest: initialRequestInfoState,
  signUpRequest: initialRequestInfoState,
  signOutRequest: initialRequestInfoState,
  resetPassRequest: initialRequestInfoState,
  createPassRequest: initialRequestInfoState,
  getProfileRequest: initialRequestInfoState,
  postOnboardingRequest: initialRequestInfoState,
  getStatusRequest: initialRequestInfoState,
}

const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    setTimeoutId(state, action) {
      state.timeoutId = action.payload
    },
    clearTimeoutId(state) {
      state.timeoutId = undefined
    },
    setUnlogged(state) {
      state.isLoggedIn = false
      state.profile = undefined
      state.role = null
    },
    setProfile(state, action) {
      state.profile = action.payload
    },
    clearRequestProfile(state) {
      state.getProfileRequest = { ...initialRequestInfoState }
    },
    setVacation(state, action) {
      state.getProfileRequest = {
        ...state,
        status: 'idle',
        isError: true,
        errorInfo: {
          detail: 'vacation',
          end_date: action.payload,
          message: '',
        },
      }
      state.role = null
    },
  },
  extraReducers: (builder) => {
    builder
      // SIGN IN
      .addCase(signIn.pending, (state) => {
        state.signInRequest.status = 'pending'
        state.signInRequest.isError = false
        state.signInRequest.errorInfo = null
      })
      .addCase(signIn.fulfilled, (state, action) => {
        state.signInRequest.status = 'succeeded'
        state.isLoggedIn = true
        state.role = action.payload?.role.toLowerCase()
      })
      .addCase(signIn.rejected, (state, action) => {
        state.signInRequest.isError = true
        state.signInRequest.errorInfo = action.payload as IAPIError
        state.signInRequest.status = 'failed'
      })

      // SIGN UP
      .addCase(signUp.pending, (state) => {
        state.signUpRequest.status = 'pending'
        state.signUpRequest.isError = false
        state.signUpRequest.errorInfo = null
      })
      .addCase(signUp.fulfilled, (state, action) => {
        state.signUpRequest.status = 'succeeded'
      })
      .addCase(signUp.rejected, (state, action) => {
        state.signUpRequest.isError = true
        state.signUpRequest.errorInfo = action.payload as IAPIError
        state.signUpRequest.status = 'failed'
      })

      // SIGN OUT
      .addCase(signOut.pending, (state) => {
        state.signOutRequest.status = 'pending'
        state.signOutRequest.isError = false
        state.signOutRequest.errorInfo = null
      })
      .addCase(signOut.fulfilled, (state) => {
        state.isLoggedIn = false
        state.profile = undefined
        state.role = null
        state.signOutRequest.status = 'succeeded'
      })
      .addCase(signOut.rejected, (state, action) => {
        state.isLoggedIn = false
        state.profile = undefined
        state.role = null
        state.signOutRequest.isError = true
        state.signOutRequest.errorInfo = action.payload as IAPIError
        state.signOutRequest.status = 'failed'
      })

      // RESET PASSWORD
      .addCase(resetPassword.pending, (state) => {
        state.resetPassRequest.status = 'pending'
        state.resetPassRequest.isError = false
        state.resetPassRequest.errorInfo = null
      })
      .addCase(resetPassword.fulfilled, (state, action) => {
        state.resetPassRequest.status = 'succeeded'
      })
      .addCase(resetPassword.rejected, (state, action) => {
        state.resetPassRequest.isError = true
        state.resetPassRequest.errorInfo = action.payload as IAPIError
        state.resetPassRequest.status = 'failed'
      })

      // CREATE PASSWORD
      .addCase(createPassword.pending, (state) => {
        state.createPassRequest.status = 'pending'
        state.createPassRequest.isError = false
        state.createPassRequest.errorInfo = null
      })
      .addCase(createPassword.fulfilled, (state, action) => {
        state.createPassRequest.status = 'succeeded'
      })
      .addCase(createPassword.rejected, (state, action) => {
        state.createPassRequest.isError = true
        state.createPassRequest.errorInfo = action.payload as IAPIError
        state.createPassRequest.status = 'failed'
      })

      // GET PROFILE
      .addCase(getProfile.pending, (state) => {
        state.getProfileRequest.status = 'pending'
        state.getProfileRequest.isError = false
        state.getProfileRequest.errorInfo = null
      })
      .addCase(getProfile.fulfilled, (state, action) => {
        state.getProfileRequest.status = 'succeeded'
        state.profile = { ...action.payload }
      })
      .addCase(getProfile.rejected, (state, action) => {
        const payload = action.payload as IAPIError
        if (payload?.end_date) {
          state.profile = undefined
        } else {
          state.profile = null
        }
        state.getProfileRequest.isError = true
        state.getProfileRequest.errorInfo = payload
        state.getProfileRequest.status = 'failed'
      })

      // GET STATUS
      .addCase(getStatus.pending, (state) => {
        state.getStatusRequest.status = 'pending'
        state.getStatusRequest.isError = false
        state.getStatusRequest.errorInfo = null
      })
      .addCase(getStatus.fulfilled, (state, action) => {
        state.getStatusRequest.status = 'succeeded'
        if (state.profile) state.profile = { ...state.profile, status: action.payload[0].value }
      })
      .addCase(getStatus.rejected, (state, action) => {
        const payload = action.payload as IAPIError
        if (payload?.end_date) {
          state.profile = undefined
        } else {
          state.profile = null
        }
        state.getStatusRequest.isError = true
        state.getStatusRequest.errorInfo = payload
        state.getStatusRequest.status = 'failed'
      })

      // POST ONBOARDING
      .addCase(postOnboarding.pending, (state) => {
        state.postOnboardingRequest.status = 'pending'
        state.postOnboardingRequest.isError = false
        state.postOnboardingRequest.errorInfo = null
      })
      .addCase(postOnboarding.fulfilled, (state, action) => {
        state.postOnboardingRequest.status = 'succeeded'
        state.profile = action.payload
      })
      .addCase(postOnboarding.rejected, (state, action) => {
        state.postOnboardingRequest.status = 'failed'
        state.postOnboardingRequest.isError = true
        state.postOnboardingRequest.errorInfo = action.payload as IAPIError
      })
  },
})

export const { setUnlogged, setProfile, clearRequestProfile, setVacation, setTimeoutId, clearTimeoutId } =
  authSlice.actions

export default authSlice
