import { AnyAction, MiddlewareAPI } from 'redux'
import websocketSlice from '../slices/websocket-slice'
import showAchievementsNotifications from '../../utils/showAchievementsNotifications'
import { ApiStage } from '../types/achievements/achievements-types'

const { wsConnectionStart, wsConnectionStop, wsConnectionSuccess, wsConnectionError, wsConnectionClosed } =
  websocketSlice.actions

export const socketMiddleware = () => (store: MiddlewareAPI) => {
  let socket: WebSocket | null = null

  return (next: (action: AnyAction) => void) => (action: AnyAction) => {
    const { dispatch } = store
    const { type, payload } = action

    if (type === wsConnectionStart.type) {
      const wsUrl: string = payload.token ? `${payload.url}?token=${payload.token}` : `${payload.url}`
      socket = new WebSocket(wsUrl)
    }

    if (type === wsConnectionStop.type && socket) {
      socket.close(1000, 'CLOSE_NORMAL')
    }

    if (socket) {
      socket.onopen = () => {
        dispatch(wsConnectionSuccess())
      }

      socket.onerror = () => {
        dispatch(wsConnectionError())
      }

      socket.onmessage = (event) => {
        const { data } = event
        const { success, ...parsedData } = JSON.parse(data)
        if (parsedData.message && parsedData.message === 'Invalid or missing token') {
          if (socket) socket.close(1000, 'CLOSE_NORMAL')
        }
        const stages = parsedData?.achievements
        if (stages) {
          showAchievementsNotifications(stages, dispatch)
          stages.forEach((stage: ApiStage) => {
            socket?.send(JSON.stringify({ success_id: stage?.success_id }))
          })
        }
      }

      socket.onclose = (event) => {
        dispatch(wsConnectionClosed())
      }
    }

    next(action)
  }
}
