import { createSlice } from '@reduxjs/toolkit'
import { Notification } from '../types/global-types'

interface IAppState {
  achievementNotifications: Array<Notification>
}

export const initialState: IAppState = {
  achievementNotifications: [],
}

const appSlice = createSlice({
  name: 'app',
  initialState,
  reducers: {
    enqueueSnackbar(state, action) {
      let notification = action.payload
      const key = notification.options && notification.options.key
      notification = {
        ...notification,
        key: key || new Date().getTime() + Math.random(),
      }
      state.achievementNotifications.push(notification)
    },
    closeSnackbar(state, action) {
      // const dismissAll = !action.payload.key
      // state.achievementNotifications = state.achievementNotifications.map((notification) =>
      //   dismissAll || notification.key === action.payload.key
      //     ? { ...notification, dismissed: true }
      //     : { ...notification },
      // )
      if (!action.payload) {
        state.achievementNotifications = []
      } else {
        state.achievementNotifications = state.achievementNotifications.filter(
          (notification) => notification.key !== action.payload,
        )
      }
    },
    removeSnackbar(state, action) {
      const dismissAll = !action.payload
      state.achievementNotifications = state.achievementNotifications.map((notification) =>
        dismissAll || notification.key === action.payload ? { ...notification, dismissed: true } : { ...notification },
      )
    },
  },
})

export const { enqueueSnackbar, closeSnackbar, removeSnackbar } = appSlice.actions

export default appSlice
