import { createAsyncThunk } from '@reduxjs/toolkit'
import { AxiosError } from 'axios'
import { API_STATS_ENDPOINT } from '../../utils/constants'
import api from './settings'

export const getNotifications = createAsyncThunk(
  'metrics/getNotifications',
  async (url: string, { rejectWithValue }) => {
    try {
      const response = await api.get(url)
      return response.data
    } catch (error) {
      const typedError = error as AxiosError
      return rejectWithValue(typedError?.response?.data)
    }
  },
)

export const getStats = createAsyncThunk('metrics/getStats', async (id: string | undefined, { rejectWithValue }) => {
  try {
    let url = API_STATS_ENDPOINT
    if (id) url += `?profile_id=${id}`
    const response = await api.get(url)
    return response.data
  } catch (error) {
    const typedError = error as AxiosError
    return rejectWithValue(typedError?.response?.data)
  }
})
