import { createAsyncThunk } from '@reduxjs/toolkit'
import { AxiosError } from 'axios'
import { API_ACHIEVEMENTS_ENDPOINT } from '../../utils/constants'
import api from './settings'

export const getAchievements = createAsyncThunk(
  'achievement/getAchievements',
  async (id: string | undefined, { rejectWithValue }) => {
    try {
      let url = API_ACHIEVEMENTS_ENDPOINT
      if (id) url += `?profile_id=${id}`
      const response = await api.get(url)
      return response.data
    } catch (error) {
      const typedError = error as AxiosError
      return rejectWithValue(typedError?.response?.data)
    }
  },
)
