// import { DiaryPage, FeedPage, MetricsPage, VacanciesPage } from '../../pages/students'
// import {
//   ApplicationsPage,
//   StudentsListPage,
//   StudentsListPageNew,
//   StudentsDiaryPage,
//   ConsultantFeedPage,
//   ConsultantMetricsPage,
//   CohortPage,
// } from '../../pages/consultants'
// import LandingPage from '../../pages/landing/landing'

// import VacanciesListPage from '../../pages/vacancies/VacanciesListPage/VacanciesListPage'
// import TestPage from '../../pages/students/TestPage/TestPage'
