// import StatisticsPage from '../../pages/consultants/StatisticsPage/StatisticsPage'

// import { ServerError404Page, ServerError500Page } from '../../pages/ServerErrorPages'

const OnboardingPage = loadable(() => import('../../pages/students/OnboardingPage/OnboardingPage'), {
  fallback: <LoadingOverlay />,
})
const OnboardingFormPersonal = loadable(
  () => import('../Main/OnboardingPage/OnboardingFormPersonal/OnboardingFormPersonal'),
  { fallback: <LoadingOverlay /> },
)
const OnboardingFormExperience = loadable(
  () => import('../Main/OnboardingPage/OnboardingFormExperience/OnboardingFormExperience'),
  { fallback: <LoadingOverlay /> },
)
const OnboardingFormResume = loadable(
  () => import('../Main/OnboardingPage/OnboardingFormResume/OnboardingFormResume'),
  { fallback: <LoadingOverlay /> },
)

const LoginPage = loadable(() => import('../../pages/auth/LoginPage/LoginPage'), { fallback: <LoadingOverlay /> })
const RegisterPage = loadable(() => import('../../pages/auth/RegisterPage/RegisterPage'), {
  fallback: <LoadingOverlay />,
})
const ResetPassword = loadable(() => import('../../pages/auth/ResetPassword/ResetPassword'), {
  fallback: <LoadingOverlay />,
})

const DiaryPage = loadable(() => import('../../pages/students/DiaryPage/DiaryPage'), { fallback: <LoadingOverlay /> })
const FeedPage = loadable(() => import('../../pages/students/FeedPage/FeedPage'), { fallback: <LoadingOverlay /> })
const MetricsPage = loadable(() => import('../../pages/students/MetricsPage/MetricsPage'), {
  fallback: <LoadingOverlay />,
})
const VacanciesPage = loadable(() => import('../../pages/students/VacanciesPage/VacanciesPage'), {
  fallback: <LoadingOverlay />,
})

const ApplicationsPage = loadable(() => import('../../pages/consultants/ApplicationsPage/ApplicationsPage'), {
  fallback: <LoadingOverlay />,
})
const StudentsListPage = loadable(() => import('../../pages/consultants/StudentsListPage/StudentsListPage'), {
  fallback: <LoadingOverlay />,
})
const StudentsListPageNew = loadable(() => import('../../pages/consultants/StudentsListPageNew/StudentsListPageNew'), {
  fallback: <LoadingOverlay />,
})
const StudentsDiaryPage = loadable(() => import('../../pages/consultants/StudentsDiaryPage/StudentsDiaryPage'), {
  fallback: <LoadingOverlay />,
})
const ConsultantFeedPage = loadable(() => import('../../pages/consultants/ConsultantFeedPage/ConsultantFeedPage'), {
  fallback: <LoadingOverlay />,
})
const ConsultantMetricsPage = loadable(
  () => import('../../pages/consultants/ConsultantMetricsPage/ConsultantMetricsPage'),
  { fallback: <LoadingOverlay /> },
)
const CohortPage = loadable(() => import('../../pages/consultants/CohortPage/CohortPage'), {
  fallback: <LoadingOverlay />,
})
const LandingPage = loadable(() => import('../../pages/landing/landing'), { fallback: <LoadingOverlay /> })
const VacanciesListPage = loadable(() => import('../../pages/vacancies/VacanciesListPage/VacanciesListPage'), {
  fallback: <LoadingOverlay />,
})
const TestPage = loadable(() => import('../../pages/students/TestPage/TestPage'), { fallback: <LoadingOverlay /> })
const ContactsPage = loadable(() => import('../../pages/students/ContactsPage/ContactsPage'), {
  fallback: <LoadingOverlay />,
})
const StatisticsPage = loadable(() => import('../../pages/consultants/StatisticsPage/StatisticsPage'), {
  fallback: <LoadingOverlay />,
})
const ServerError404Page = loadable(() => import('../../pages/ServerErrorPages/ServerError404Page/ServerError404Page'))
const ServerError500Page = loadable(() => import('../../pages/ServerErrorPages/ServerError500Page/ServerError500Page'))

const AuthLayout = loadable(() => import('../Layouts/AuthLayout/AuthLayout'))
const MainLayout = loadable(() => import('../Layouts/MainLayout/MainLayout'))
const DiaryLayout = loadable(() => import('../Layouts/DiaryLayout/DiaryLayout'))
const PartnersLayout = loadable(() => import('../Layouts/PartnersLayout/PartnersLayout'))
const RequireUserAuth = loadable(() => import('../RequireAuth/RequireUserAuth/RequireUserAuth'))
const RequireConsultantAuth = loadable(() => import('../RequireAuth/RequireConsultantAuth/RequireConsultantAuth'))
const RequireVacancyAuth = loadable(() => import('../RequireAuth/RequireVacancyAuth/RequireVacancyAuth'))

SwiperCore.use([Pagination])

function App() {
  const classes = useStyles()
  const { role, timeoutId } = useAppSelector((store) => store.auth)
  const dispatch = useAppDispatch()
  const { updateStudentStatus } = useUpdateStudentStatus()
  const themeOptions = React.useMemo(
    () =>
      createTheme({
        palette: {
          ...theme.palette,
        },
        typography: {
          ...theme.typography,
        },
        overrides: {
          MuiButton: {
            root: {
              width: '100%',
              minHeight: '50px',
              backgroundColor: theme.palette.button.primary,
              color: theme.palette.button.primaryTextColor,
              '&:hover': {
                backgroundColor: theme.palette.button.primaryHover,
              },
            },
            label: {
              color: theme.palette.button.primaryTextColor,
            },
            textPrimary: {
              '&:hover': {
                backgroundColor: 'none',
              },
            },
          },
        },
      }),
    [theme.palette],
  )
  const [snack, setSnack] = React.useState({
    open: false,
    header: ' ',
    message: '',
  })

  const { REACT_APP_IS_DEVTEST_SERVER } = process.env

  //      ,  
  useEffect(() => {
    if (role === 'user') updateStudentStatus()
    return () => {
      clearTimeout(timeoutId)
      dispatch(clearTimeoutId())
    }
  }, [role])

  return (
    <ThemeProvider theme={themeOptions}>
      <SnackbarProvider>
        <Routes>
          <Route index path="/" element={<LandingPage />} />
          {/*     (  AuthLayout:  ,  ) */}
          <Route path="/" element={<AuthLayout />}>
            <Route
              path="sign-in"
              element={
                <main className={classes.root}>
                  <LoginPage />
                </main>
              }
            />
            <Route
              path="sign-up"
              element={
                <main className={classes.root}>
                  <RegisterPage />
                </main>
              }
            />
            <Route
              path="reset-password"
              element={
                <main className={classes.root}>
                  <ResetPassword />
                </main>
              }
            />
          </Route>
          <Route path="onboarding">
            <Route index element={<Navigate to="/onboarding/personal-info" />} />
            <Route
              path="personal-info"
              element={
                <RequireUserAuth>
                  <main className={classes.rootSigned}>
                    <OnboardingPage title="    ,      ">
                      <OnboardingFormPersonal />
                    </OnboardingPage>
                  </main>
                </RequireUserAuth>
              }
            />
            <Route
              path="experience"
              element={
                <RequireUserAuth>
                  <main className={classes.rootSigned}>
                    <OnboardingPage title="         ">
                      <OnboardingFormExperience />
                    </OnboardingPage>
                  </main>
                </RequireUserAuth>
              }
            />
            <Route
              path="resume"
              element={
                <RequireUserAuth>
                  <main className={classes.rootSigned}>
                    <OnboardingPage
                      title={[
                        '            .',
                        <br />,
                        '       ',
                      ]}
                    >
                      <OnboardingFormResume />
                    </OnboardingPage>
                  </main>
                </RequireUserAuth>
              }
            />
          </Route>
          {/*    (  DiaryLayout:  ,  ,  ) */}
          <Route path="/" element={<DiaryLayout snack={snack} setSnack={setSnack} />}>
            <Route
              index
              element={
                <RequireUserAuth>
                  <DiaryPage setSnack={setSnack} />
                </RequireUserAuth>
              }
            />
            <Route
              path="diary"
              element={
                <RequireUserAuth>
                  <DiaryPage setSnack={setSnack} />
                </RequireUserAuth>
              }
            />
            {REACT_APP_IS_DEVTEST_SERVER && (
              <Route
                path="test-page"
                element={
                  <RequireUserAuth>
                    <TestPage />
                  </RequireUserAuth>
                }
              />
            )}
            <Route
              path="feed"
              element={
                <RequireUserAuth>
                  <FeedPage setSnack={setSnack} />
                </RequireUserAuth>
              }
            />
            <Route
              path="contacts"
              element={
                <RequireUserAuth>
                  <ContactsPage setSnack={setSnack} />
                </RequireUserAuth>
              }
            />
            <Route
              path="metrics"
              element={
                <RequireUserAuth>
                  <MetricsPage setSnack={setSnack} />
                </RequireUserAuth>
              }
            />
          </Route>
          <Route path="/vacancies" element={<PartnersLayout snack={snack} setSnack={setSnack} />}>
            <Route
              index
              element={
                <RequireUserAuth>
                  <VacanciesPage setSnack={setSnack} />
                </RequireUserAuth>
              }
            />
          </Route>
          {/*    */}
          <Route path="/consultant" element={<MainLayout snack={snack} setSnack={setSnack} />}>
            <Route index element={<Navigate to="/consultant/applications" />} />
            <Route
              path="applications"
              element={
                <RequireConsultantAuth>
                  <ApplicationsPage setSnack={setSnack} />
                </RequireConsultantAuth>
              }
            />
            <Route
              path="test-page"
              element={
                <RequireConsultantAuth>
                  <TestPage />
                </RequireConsultantAuth>
              }
            />
            <Route
              path="students"
              element={
                <RequireConsultantAuth>
                  <StudentsListPageNew setSnack={setSnack} />
                </RequireConsultantAuth>
              }
            />
            <Route
              path="students2"
              element={
                <RequireConsultantAuth>
                  <StudentsListPage setSnack={setSnack} />
                </RequireConsultantAuth>
              }
            />
            <Route
              path="statistics"
              element={
                <RequireConsultantAuth>
                  <StatisticsPage setSnack={setSnack} />
                </RequireConsultantAuth>
              }
            />
            <Route
              path="feed"
              element={
                <RequireConsultantAuth>
                  <ConsultantFeedPage setSnack={setSnack} />
                </RequireConsultantAuth>
              }
            >
              <Route
                path=":studentId"
                element={
                  <RequireConsultantAuth>
                    <ConsultantFeedPage setSnack={setSnack} />
                  </RequireConsultantAuth>
                }
              />
            </Route>
            <Route
              path="diary"
              element={
                <RequireConsultantAuth>
                  <StudentsDiaryPage setSnack={setSnack} />
                </RequireConsultantAuth>
              }
            />
            <Route
              path="metrics"
              element={
                <RequireConsultantAuth>
                  <ConsultantMetricsPage />
                </RequireConsultantAuth>
              }
            />
            <Route
              path="cohorts"
              element={
                <RequireConsultantAuth>
                  <CohortPage setSnack={setSnack} />
                </RequireConsultantAuth>
              }
            />
          </Route>
          {/*      */}
          <Route path="/vacancies" element={<MainLayout snack={snack} setSnack={setSnack} />}>
            <Route index element={<Navigate to="/vacancies/list" />} />
            <Route
              path="list"
              element={
                <RequireVacancyAuth>
                  <VacanciesListPage setSnack={setSnack} />
                </RequireVacancyAuth>
              }
            />
          </Route>
          <Route path="500" element={<ServerError500Page />} />
          {/*      404 */}
          <Route path="*" element={<ServerError404Page />} />
        </Routes>
      </SnackbarProvider>
    </ThemeProvider>
  )
}

export default App
