import { createSlice } from '@reduxjs/toolkit'
import { getStats, getNotifications } from '../axios/metrics'
import { IMetricsState, IAPIError } from '../types/metrics/metrics-types'

export const initialState: IMetricsState = {
  status: 'idle',
  isError: false,
  errorInfo: null,
  notification: null,
  funnelStats: null,
  eventsStats: [],
}

const metricsSlice = createSlice({
  name: 'metrics',
  initialState,
  reducers: {
    setNotificated(state) {
      if (state.notification) {
        state.notification.is_notificated = true
      }
    },
    resetMetrics: () => initialState,
  },
  extraReducers: (builder) => {
    builder

      // GET NOTIFICATIONS
      .addCase(getNotifications.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
      })
      .addCase(getNotifications.fulfilled, (state, action) => {
        state.status = 'succeeded'
        state.notification = { ...action.payload }
      })
      .addCase(getNotifications.rejected, (state, action) => {
        const payload = action.payload as IAPIError
        state.isError = true
        state.errorInfo = payload
        state.status = 'failed'
      })

      // GET STATS
      .addCase(getStats.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
      })
      .addCase(getStats.fulfilled, (state, action) => {
        state.status = 'succeeded'
        const { all_stats, week_stats } = action.payload
        if (all_stats) {
          state.funnelStats = { ...all_stats }
        }
        if (week_stats) {
          state.eventsStats = [...week_stats]
        }
      })
      .addCase(getStats.rejected, (state, action) => {
        const payload = action.payload as IAPIError
        state.isError = true
        state.errorInfo = payload
        state.status = 'failed'
      })
  },
})

export const { setNotificated, resetMetrics } = metricsSlice.actions

export default metricsSlice
