import { createSlice } from '@reduxjs/toolkit'
import { v4 } from 'uuid'
import { getAchievements } from '../axios/achievements'
import { IAPIError, IRequestInfo } from '../types/global-types'
import medal from '../../assets/medal-big-gray.svg'
import { ApiAchievement, TAchievement } from '../types/achievements/achievements-types'

interface IAchievementState extends IRequestInfo {
  selectedAchievement: TAchievement & {
    open: boolean
  }
  allAchievements: Array<TAchievement>
  successAchievements: Array<TAchievement>
  failedAchievements: Array<TAchievement>
  commonAchievements: Array<TAchievement>
  preaccelerationAchievements: Array<TAchievement>
  accelerationAchievements: Array<TAchievement>
  testPeriodAchievements: Array<TAchievement>
  communityAchievements: Array<TAchievement>
  filteredAllAchievements: Array<TAchievement>
  filteredCommonAchievements: Array<TAchievement>
  filteredPreaccelerationAchievements: Array<TAchievement>
  filteredAccelerationAchievements: Array<TAchievement>
  filteredTestPeriodAchievements: Array<TAchievement>
  filteredCommunityAchievements: Array<TAchievement>
}

export const initialState: IAchievementState = {
  status: 'idle',
  isError: false,
  errorInfo: null,
  selectedAchievement: {
    image: '',
    title: '',
    message: '',
    open: false,
    isSuccess: false,
  },
  allAchievements: [],
  commonAchievements: [],
  preaccelerationAchievements: [],
  accelerationAchievements: [],
  testPeriodAchievements: [],
  communityAchievements: [],
  successAchievements: [],
  failedAchievements: [],
  filteredAllAchievements: [],
  filteredCommonAchievements: [],
  filteredPreaccelerationAchievements: [],
  filteredAccelerationAchievements: [],
  filteredTestPeriodAchievements: [],
  filteredCommunityAchievements: [],
}

const achievementsSlice = createSlice({
  name: 'achievement',
  initialState,
  reducers: {
    openActiveAchievement(state, action) {
      state.selectedAchievement = { ...action.payload, open: true }
    },
    closeActiveAchievement(state) {
      state.selectedAchievement.open = false
    },
    filterAchievements(state, action) {
      switch (action.payload) {
        case 1:
          //  
          state.filteredAllAchievements = [...state.allAchievements]
          state.filteredCommonAchievements = [...state.commonAchievements]
          state.filteredPreaccelerationAchievements = [...state.preaccelerationAchievements]
          state.filteredAccelerationAchievements = [...state.accelerationAchievements]
          state.filteredTestPeriodAchievements = [...state.testPeriodAchievements]
          state.filteredCommunityAchievements = [...state.communityAchievements]
          break
        case 2:
          //  
          state.filteredAllAchievements = [...state.successAchievements]
          state.filteredCommonAchievements = state.commonAchievements.filter((item) => item.isSuccess)
          state.filteredPreaccelerationAchievements = state.preaccelerationAchievements.filter((item) => item.isSuccess)
          state.filteredAccelerationAchievements = state.accelerationAchievements.filter((item) => item.isSuccess)
          state.filteredTestPeriodAchievements = state.testPeriodAchievements.filter((item) => item.isSuccess)
          state.filteredCommunityAchievements = state.communityAchievements.filter((item) => item.isSuccess)
          break
        case 3:
          //  
          state.filteredAllAchievements = [...state.failedAchievements]
          state.filteredCommonAchievements = state.commonAchievements.filter((item) => !item.isSuccess)
          state.filteredPreaccelerationAchievements = state.preaccelerationAchievements.filter(
            (item) => !item.isSuccess,
          )
          state.filteredAccelerationAchievements = state.accelerationAchievements.filter((item) => !item.isSuccess)
          state.filteredTestPeriodAchievements = state.testPeriodAchievements.filter((item) => !item.isSuccess)
          state.filteredCommunityAchievements = state.communityAchievements.filter((item) => !item.isSuccess)
          break
        default:
          //   ,   
          break
      }
    },
    resetAchievements: () => initialState,
  },
  extraReducers: (builder) => {
    builder
      .addCase(getAchievements.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
      })
      .addCase(getAchievements.fulfilled, (state, action) => {
        state.status = 'succeeded'
        const achievementGroups = action.payload
        state.allAchievements = []
        state.commonAchievements = []
        state.preaccelerationAchievements = []
        state.accelerationAchievements = []
        state.testPeriodAchievements = []
        state.communityAchievements = []
        achievementGroups.forEach((group: ApiAchievement) => {
          const statuses = group.required_statuses
          group.stages.forEach((stage) => {
            const uuid = v4()
            const achievement: TAchievement = stage.is_hidden
              ? {
                  image: medal,
                  title: ' ',
                  message: ' ',
                  uuid,
                  isSuccess: stage.is_success,
                }
              : {
                  image: stage.is_success ? stage.success_image : stage.todo_image,
                  title: stage.name,
                  message: stage.condition_text,
                  currentProgress: stage.current_progress,
                  maxProgress: stage.success_condition,
                  uuid,
                  isSuccess: stage.is_success,
                }
            //       
            if (statuses.length === 4) {
              state.commonAchievements.push(achievement)
              state.filteredCommonAchievements.push(achievement)
            } else {
              if (statuses.includes(1)) {
                state.preaccelerationAchievements.push(achievement)
                state.filteredPreaccelerationAchievements.push(achievement)
              }
              if (statuses.includes(2)) {
                state.accelerationAchievements.push(achievement)
                state.filteredAccelerationAchievements.push(achievement)
              }
              if (statuses.includes(3)) {
                state.communityAchievements.push(achievement)
                state.filteredCommunityAchievements.push(achievement)
              }
              if (statuses.includes(7)) {
                state.testPeriodAchievements.push(achievement)
                state.filteredTestPeriodAchievements.push(achievement)
              }
            }
            //        
            if (stage.is_success) {
              state.successAchievements.push(achievement)
            } else {
              state.failedAchievements.push(achievement)
            }
            state.allAchievements.push(achievement)
            state.filteredAllAchievements.push(achievement)
          })
        })
      })
      .addCase(getAchievements.rejected, (state, action) => {
        state.isError = true
        state.errorInfo = action.payload as IAPIError
        state.status = 'failed'
      })
  },
})

export const { openActiveAchievement, closeActiveAchievement, resetAchievements, filterAchievements } =
  achievementsSlice.actions

export default achievementsSlice
