import { Middleware } from '@reduxjs/toolkit'
import showAchievementsNotifications from '../../utils/showAchievementsNotifications'
import { resetPartnersAchievements } from '../slices/student-vacancies-slice'

export const authMiddleware: Middleware =
  ({ dispatch, getState }) =>
  (next) =>
  (action) => {
    const result = next(action)
    if (action.type?.startsWith('auth/')) {
      const authState = getState().auth
      const stateToSave = { isLoggedIn: authState.isLoggedIn, role: authState.role, profile: authState.profile }
      localStorage.setItem('auth', JSON.stringify(stateToSave))
      // /          
      if (authState.role === 'consultant' || authState.role === 'vacancy_manager')
        localStorage.setItem('metrics', JSON.stringify({ hideMetrics: false }))
      if (authState.role === null && localStorage.getItem('metrics')) localStorage.removeItem('metrics')
    }
    const studentVacanciesState = getState().studentVacancies
    if (studentVacanciesState.achievements.length > 0) {
      showAchievementsNotifications(studentVacanciesState.achievements, dispatch)
      dispatch(resetPartnersAchievements())
    }
    return result
  }
