import { createSlice } from '@reduxjs/toolkit'
import { GridRowsProp } from '@mui/x-data-grid'
import {
  IDirectories,
  IFilterJson,
  IInitialState,
  IStudentRawData,
  TStudentRawArrayData,
  TStudentRowArray,
} from '../types/vacacies-page-types/vacancies-page-types'
import {
  getDirectories,
  getInfo,
  getVacanciesList,
  patchStudentStatus,
  patchVacancyStatus,
} from '../axios/vacancies-page'
import { IAPIError } from '../types/global-types'

const getRows = (rawData: TStudentRawArrayData): TStudentRowArray => {
  const result = []
  const isOfferedStudents = !!rawData.find((s) => s.job_found)
  const isOtherStudents = !!rawData.find((s) => !s.job_found)
  if (isOfferedStudents) result.push({ id: 'OFFERED', title: '    ' })
  rawData.map((s) => (s.job_found ? result.push(s) : null))
  if (isOtherStudents) result.push({ id: 'OTHER', title: ' ' })
  rawData.map((s) => (!s.job_found ? result.push(s) : null))
  return result
}

export const initialState: IInitialState = {
  filtersJSON: {} as IFilterJson,
  directoriesData: {} as IDirectories,
  isOpenVacancyPopup: false,
  vacancyToArchive: null,
  isInfoPopupOpen: false,
  vacancyToEdit: null,
  vacancies: [],
  isLoading: false,
  isOpenFoundJobPopup: false,
  studentToEdit: null,
  isNetworkError: false,
  networkError: null,
  infoData: [],
  rows: [],
  updateStatus: '',
  updateStudentStatus: false,
  rowsCount: 0,
}

const vacanciesPageSlice = createSlice({
  name: 'vacancies-page',
  initialState,
  reducers: {
    setFiltersJSON(state, action) {
      state.filtersJSON = action.payload
    },
    setDirectoriesData(state, action) {
      state.directoriesData = action.payload
    },
    setIsOpenVacancyPopup(state, action) {
      state.isOpenVacancyPopup = action.payload
    },
    setVacancyToArchive(state, action) {
      state.vacancyToArchive = action.payload
    },
    setIsInfoPopupOpen(state, action) {
      state.isInfoPopupOpen = action.payload
    },
    setVacancyToEdit(state, action) {
      state.vacancyToEdit = action.payload
    },
    setVacancies(state, action) {
      state.vacancies = action.payload
    },
    setIsLoading(state, action) {
      state.isLoading = action.payload
    },
    setIsOpenFoundJobPopup(state, action) {
      state.isOpenFoundJobPopup = action.payload
    },
    setStudentToEdit(state, action) {
      state.studentToEdit = action.payload
    },
    resetUpdateStatus(state) {
      state.updateStatus = ''
    },
    resetUpdateStudentStatus(state) {
      state.updateStudentStatus = false
    },
  },
  extraReducers: (builder) => {
    builder
      // get Vacancies
      .addCase(getVacanciesList.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getVacanciesList.fulfilled, (state, action) => {
        state.isLoading = false
        state.vacancies = action.payload.results
        state.rowsCount = action.payload.count
      })
      .addCase(getVacanciesList.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // get Directories
      .addCase(getDirectories.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getDirectories.fulfilled, (state, action) => {
        state.isLoading = false
        state.filtersJSON = action.payload.filters
        state.directoriesData = action.payload.directoriesData
      })
      .addCase(getDirectories.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // patch Vacancies
      .addCase(patchVacancyStatus.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(patchVacancyStatus.fulfilled, (state, action) => {
        state.isLoading = false
        const { updatedRow } = action.payload
        const newVacancies = [...state.vacancies]
        newVacancies[newVacancies.findIndex((el) => el.id === updatedRow.id)] = updatedRow
        state.vacancies = newVacancies
        state.updateStatus = 'ok'
      })
      .addCase(patchVacancyStatus.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // patch students
      .addCase(patchStudentStatus.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(patchStudentStatus.fulfilled, (state, action) => {
        state.isLoading = false
        const { updatedRow } = action.payload
        const newInfoRows: Array<IStudentRawData> = [...state.infoData]
        newInfoRows[newInfoRows.findIndex((el) => el.id === updatedRow.id)].status.value = updatedRow.status
        state.rows = getRows(newInfoRows)
        state.updateStatus = 'ok'
        state.updateStudentStatus = true
      })
      .addCase(patchStudentStatus.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // get Info about vacancy
      .addCase(getInfo.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getInfo.fulfilled, (state, action) => {
        state.isLoading = false
        state.infoData = action.payload
        state.rows = getRows(action.payload)
      })
      .addCase(getInfo.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })
  },
})

export const {
  setFiltersJSON,
  setDirectoriesData,
  setIsOpenVacancyPopup,
  setVacancyToArchive,
  setIsInfoPopupOpen,
  setVacancyToEdit,
  setVacancies,
  setIsLoading,
  setIsOpenFoundJobPopup,
  setStudentToEdit,
  resetUpdateStatus,
  resetUpdateStudentStatus,
} = vacanciesPageSlice.actions

export default vacanciesPageSlice
